html, body, .root-grid {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.sidebar {
  flex-direction: column;
  grid-area: 1 / 1;
  display: flex;
  overflow: scroll;
}

body {
  flex-direction: column;
  display: flex;
}

.viz-grid {
  grid-area: 1 / 3 / auto / 4;
  grid-template-rows: 1fr 10px 1fr;
  width: 100%;
  height: 100%;
  display: grid;
  overflow: hidden;
}

.root-grid {
  display: grid;
  overflow: hidden;
}

.root-sidebar-open {
  grid-template-columns: 1fr 10px 4fr;
}

.root-sidebar-closed {
  grid-template-columns: 0fr 0fr 1fr;
}

.path-viewer-column {
  flex-direction: column;
  display: flex;
}

.path-list-flex-item {
  flex-basis: 20px;
  margin: 2px;
  position: relative;
}

.path-name {
  font-family: monospace;
  overflow: hidden;
}

.path-data-canvas {
  position: absolute;
  overflow: hidden;
}

#viz-container {
  grid-template-rows: 1fr 10px 1fr;
  display: grid;
}

#graph-viewer-container {
  grid-area: 1 / 1;
}

#graph-viewer-container > canvas {
  position: absolute;
}

#path-viewer-range-input {
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.path-list-header {
  z-index: 20;
  position: sticky;
}

#tooltip {
  color: #fff;
  z-index: 100;
  pointer-events: none;
  background: #222;
  border-radius: 4px;
  width: max-content;
  padding: 5px;
  font-size: 90%;
  font-weight: bold;
  transition-property: left, top;
  transition-duration: 50ms;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

#path-viewer-container {
  z-index: 2;
  grid-area: 3 / 1;
  grid-template-columns: 1fr 10px 5fr;
  display: grid;
  overflow-x: clip;
  overflow-y: scroll;
}

.gutter-row {
  z-index: 1;
  cursor: row-resize;
  background: #eee;
}

.gutter-row-1 {
  grid-area: 2 / 1;
}

.gutter-column {
  z-index: 1;
  cursor: col-resize;
  background: #eee;
}

.gutter-column-1 {
  grid-column: 2;
}

.lds-dual-ring {
  width: 80px;
  height: 80px;
  display: inline-block;
}

.lds-dual-ring:after {
  content: " ";
  border: 6px solid #333;
  border-color: #333 #0000;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  margin: 8px;
  animation: 1.2s linear infinite lds-dual-ring;
  display: block;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.bed-panel {
  flex-direction: column;
  padding: 4px;
  display: flex;
}

.bed-file-list, .bed-file-entry {
  flex-direction: column;
  display: flex;
}

.bed-file-name {
  font-family: sans-serif;
  font-weight: bold;
}

.bed-file-row {
  flex-basis: 20px;
  grid-template-columns: 5fr 1fr 1fr;
  display: grid;
}

.bed-row-label {
  grid-column: 1;
  font-family: monospace;
}

.bed-row-1d {
  grid-column: 2;
}

.bed-row-2d {
  grid-column: 3;
}

.highlight-enabled {
  color: #111;
  background-color: #eee;
}

.highlight-disabled {
  color: #333;
  background-color: #bbb;
}

#sidebar-bed-context-menu {
  color: #333;
  z-index: 10;
  background: #ddd;
  border-radius: 4px;
  flex-direction: column;
  width: max-content;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

